import React, {Component} from 'react';
import ImageInteractivePractice from './ImageInteractivePractice';

let prob_data = null;
let testCount = null;
let audio_abbrev = 'asaf_narration';

class App extends Component {
   constructor() {
      super();
      this.state = {data : null, PNUM : null};
   }
   componentDidMount() {
      fetch('https://api.airtable.com/v0/appbJvIOkBMSrEz8z/Add%20and%20Subtract%20Any%20Fractions?api_key=keyuFtFTBZMHrzjOn')
      .then((resp) => resp.json())
      .then(data => {
      prob_data = data.records.sort(function(a, b){return a.fields.order - b.fields.order});
      testCount = 0;
      for (var a = 0; a < (Object.keys(prob_data[0].fields)).length; a++) {
      if ((Object.keys(prob_data[0].fields))[a].match(/^test\d*$/)) testCount += 1;
      }
      this.setState({data : prob_data, PNUM : testCount});
      });

   }
   render() {
      if (!prob_data && !testCount) return null;
      let primary = '#4499cc';
      let secondary = '#557722';
      const disabled = '#D3D3D3';
      let dislabel = '#aaccdd';

      let scala;
      if (this.props.media === 'images-interactive') {
         scala = <ImageInteractivePractice primcolor={primary} seccolor={secondary} discolor={disabled} dislabel={dislabel} suffix='asaf' probdata={this.state.data} pnum={this.state.PNUM} audio_abbrev={audio_abbrev} />
      }
      return (
      <div>
      {scala}
      </div>
      );
   }
}

export default App;
